

<template>
  <div>
    <div ref="keyboardCT" class="pc-kbd-ct">
      <div v-for="(row, idx) in kbdMain" :key="idx" class="pc-kbd-row">
        <div
          v-for="(item, index) in row"
          :key="index"
          :class="{
            disable: item.d == 1,
            selec: editState && item.c == selectedKey
          }"
          :style="{
            width: item.w ? `${item.w}px` : '',
            height: item.h ? `${item.h}px` : '',
            marginLeft: item.m ? `${item.m}px` : ''
          }"
          :name="item.c"
          :disable="item.d == 1"
        >
          <div class="key-name">
            {{ item.n }}
          </div>
          <div v-if="item.d !== 1" class="key-rep">
            {{ getEqualName(item.c) }}
          </div>
        </div>
      </div>
    </div>
    <div class="pc-kbd-set">
      <div class="row">
        <span> </span>
        <xw-button type="primary" tabindex="0" @click="editState = !editState">
          {{ editState ? "" : "" }}
        </xw-button>
        <span> </span>
      </div>
      <div v-if="editState" class="row edit-container">
        <p>
          PS:
          
        </p>
        <div v-if="selectedKey === 0">
          
        </div>
        <div v-else>
          <span> </span>
          <div
            :style="{
              width: getPcKeyWidth(selectedKey)
            }"
            class="select-key"
          >
            <div class="key-name">
              {{ getPcKeyName(selectedKey) }}
            </div>
          </div>
          <span></span>
          <span>{{
            getName(selectedKey)
              ? ` ${getKeyIndex(selectedKey)} ${getName(selectedKey)}`
              : ""
          }}</span>
        </div>
        <div class="btn-group">
          <span></span>
          <xw-button type="primary" @click="degradeYin">
             ()
          </xw-button>
          <span></span>
          <span></span>
          <xw-button type="primary" @click="upgradeYin">
             ()
          </xw-button>
          <span></span>
          <span></span>
          <xw-button type="primary" @click="clearConfig()">
            
          </xw-button>
          <span></span>
          <span></span>
          <xw-button type="primary" @click="restoreConfig()">
            
          </xw-button>
          <span>(C)</span>
          <span></span>
          <xw-button type="primary" @click="restoreConfig2()">
            
          </xw-button>
          <span>(C)</span>
        </div>
      </div>
      <div class="row">
        <pcKeyboardSave />
      </div>
      <div class="row">
        <pcKeyboardSaveSys />
      </div>
      <div class="row-title">
        FAQ :
      </div>
      <faq
        class="row"
        question=""
        answer="
                1.  ->  ->  ``
                2.  `` 
                "
      />
      <faq
        class="row"
        question=""
        answer=""
      />
    </div>
  </div>
</template>

<script>
/* eslint-disable no-restricted-syntax */
/* eslint-disable no-shadow */
/* eslint-disable guard-for-in */

/**
 * 
 * code
 */
import xwButton from '@/comp/xw-comp/xw-button.vue';
import xwMessage from '@/comp/xw-comp/xw-message';
import pcKeyboardSave from './pc-keyboard-save.vue';
import {
  onEvent, offEvent, keyDown, keyUp, getKeyConfig,
} from '../keyboard-pc';
import { keyNameList } from '../keyboard';
import { keypress } from '../piano-control';
import faq from '../comp/faq.vue';
import pckey2key, { pckey2keyType2 } from '../../pckey-key';

const domArray = new Array(256);
const keyNameArray = new Array(256);

const kbdMain = getKeyConfig();

kbdMain.forEach((item) => {
  item.forEach((element) => {
    if (element.d !== 1) {
      keyNameArray[element.c] = element;
    }
  });
});

export default {
  components: {
    xwButton,
    pcKeyboardSave,
    pcKeyboardSaveSys: () => import('./pc-keyboard-save-sys.vue'),
    faq,
  },
  data() {
    return {
      kbdMain,
      editState: false,
      selectedKey: 0,
    };
  },
  computed: {},

  mounted() {
    this.bindToArray();
    onEvent(this.onEvent);
    keypress.onDown(this.onKeypressDown);
  },

  destroyed() {
    offEvent(this.onEvent);
    keypress.offDown(this.onKeypressDown);
  },

  methods: {
    onKeypressDown(keys, by) {
      if (by === 1 && this.editState && this.selectedKey !== 0 && keys.length) {
        const [key] = keys;
        const {
          cacheConf: { pckey2key },
        } = this.$store.state;
        this.$set(pckey2key, this.selectedKey, key);
      }
    },
    getEqualName(pcKey) {
      const {
        cacheConf: { pckey2key },
      } = this.$store.state;
      const key = pckey2key[pcKey];
      if (key && keyNameList[key]) {
        return ` = ${keyNameList[key]}`;
      }
      return '';
    },
    getName(pcKey) {
      const {
        cacheConf: { pckey2key },
      } = this.$store.state;
      const key = pckey2key[pcKey];
      if (key && keyNameList[key]) {
        return keyNameList[key];
      }
      return '';
    },
    getKeyIndex(pcKey) {
      // pckeykey
      const {
        cacheConf: { pckey2key },
      } = this.$store.state;
      const key = pckey2key[pcKey];
      return key;
    },
    getPcKeyName(key) {
      if (keyNameArray[key]) {
        return keyNameArray[key].n;
      }
      return '';
    },
    getPcKeyWidth(key) {
      if (keyNameArray[key] && keyNameArray[key].w) {
        return `${keyNameArray[key].w}px`;
      }
      return '';
    },
    bindDomClickEvent(dom, key) {
      let isdown = false;
      dom.addEventListener(
        'mousedown',
        (e) => {
          isdown = true;
          keyDown(key);
          e.stopPropagation();
          e.preventDefault();
        },
        true,
      );
      function up(e) {
        if (isdown) {
          isdown = false;
          keyUp(key);
        }
        e.stopPropagation();
        e.preventDefault();
      }
      dom.addEventListener('mouseleave', up, true);
      dom.addEventListener('mouseup', up, true);
    },
    bindCommentEvent() {},
    bindToArray() {
      const { keyboardCT } = this.$refs;
      for (let index = 0; index < keyboardCT.children.length; index += 1) {
        const element = keyboardCT.children[index];
        for (let j = 0; j < element.children.length; j += 1) {
          const element2 = element.children[j];
          if (!element2.getAttribute('disable')) {
            const name = element2.getAttribute('name');
            const key = parseInt(name, 10);
            domArray[key] = element2;
            this.bindDomClickEvent(element2, key);
          }
        }
      }
    },
    onEvent(isDown, which) {
      const element = domArray[which];
      if (element) {
        if (isDown) {
          element.classList.add('d');
          if (this.editState) {
            this.selectedKey = which;
          }
        } else {
          element.classList.remove('d');
        }
      }
    },
    upgradeYin() {
      const {
        cacheConf: { pckey2key },
      } = this.$store.state;
      for (const i in pckey2key) {
        const t = parseInt(i, 10);
        if (t) {
          pckey2key[t] += 1;
        }
      }
      xwMessage.success('');
    },
    degradeYin() {
      const {
        cacheConf: { pckey2key },
      } = this.$store.state;
      for (const i in pckey2key) {
        const t = parseInt(i, 10);
        if (t) {
          pckey2key[t] -= 1;
        }
      }
      xwMessage.success('');
    },
    clearConfig() {
      this.$store.state.cacheConf.pckey2key = {};
      xwMessage.success('');
    },
    restoreConfig() {
      this.$store.state.cacheConf.pckey2key = pckey2key();
      xwMessage.success('');
    },
    restoreConfig2() {
      // 
      this.$store.state.cacheConf.pckey2key = pckey2keyType2();
      xwMessage.success('');
    },
  },
};
</script>

<style lang="stylus">
.pc-kbd-ct {
    padding 30px 5px 15px;
    user-select none;
    width 1272px;
    margin 0 auto;
    >.pc-kbd-row {
        display block;
        white-space nowrap;
        height 52px;
        margin 0 0 5px;
        >div {
            display inline-block;
            width 50px;
            height 50px;
            margin 0 5px 0 0;
            background #fff;
            border 1px solid #0000004d;
            border-radius 4px;
            line-height 1;
            vertical-align top;
            &.disable {
                // background #ccc;
                background #7d97ad;
                border 1px solid #0003;
            }
            &.d {
                background-color #c7274c80;
            }
            &.selec {
                animation shanshan 1s infinite;
            }
            >.key-name {
                padding 5px 8px 0;
                pointer-events none;
            }
            >.key-rep {
                font-size 12px;
                padding 6px 0 0 0px;
                color #100affcc;
                font-weight bold;
                text-align center;
                pointer-events none;
            }
        }
    }

}
.pc-kbd-set {
    width 1272px;
    margin 0 auto;
    padding 0 0 50px 0;
    >.row-title {
        margin 0 10px 0.5em
        font-size 16px
        font-weight bold
    }
    >.row {
        margin 0 10px;
        &.faq-item{
            margin 0 10px 1em;
        }
    }
    .xw-button{
        font-size 14px;
        margin 0 0.25em;
    }
    .select-key{
        display inline-block;
        width 50px;
        height 50px;
        margin 0 5px 0 0;
        background #000b;
        border 1px solid #0000004d;
        border-radius 4px;
        line-height 1;
        vertical-align top;
        >.key-name {
            padding 5px 8px 0;
            pointer-events none;
            font-weight bold;
            // color #100affcc;
            color #fff;
        }
    }

    .edit-container {
        margin 5px 0 0 0;
        border 1px solid #0003;
        padding 3px 16px 20px;
        background #fff;
        >.btn-group {
            margin 10px 0 0 0;
        }
    }
}


@keyframes shanshan {
    0% {
        border 1px solid #100affcc;
        color #100affcc;
        background-color #100aff33;
    }
    49% {
        border 1px solid #100affcc;
        color #100affcc;
        background-color #100aff33;
    }
    50% {
        border 1px solid #0000004d;
        color #000;
    }
    100% {
        border 1px solid #0000004d;
        color #000;
    }
}
</style>
