import Vue from 'vue';
import Vuex from 'vuex';

import pckey2key from './pckey-key';

Vue.use(Vuex);

export default function () {
  const store = new Vuex.Store({
    state: {
      mainDoc: '', // 
      hideNavBar: false, // 
      cacheConf: {
        showKbdNum: true, // 
        scale: 1, // barScale1-20
        barScale: 1, // 
        left: 0.5, // 0-1
        pckey2key: pckey2key(),
      },
    },

    mutations: {
      setData(state, data) {
        state.mainDoc = data && data.doc;
      },
    },

    actions: {},
  });

  return store;
}
